<template>
  <div class="page-container">
    <div class="card card-primary card-outline appointment-form">
      <div class="card-body box-profile">
        <div class="text-center">
          <img @click="uploadFile" class="profile-user-img img-fluid img-circle face-circle" :src="face_img ? face_img : defaultImg" alt="User profile picture" />
          <input type="file" accept="image/*" ref="uploadFile" @change="change($event)" hidden />
        </div>
        <!-- <h3 class="profile-username text-center">上传照片</h3> -->
        <div class="form-body">
          <div class="form-group">
            <label>身份证号:</label>
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text"><i class="far fa-clock"></i></span>
              </div>
              <input type="text" class="form-control float-right" v-model="id_card_number" />
            </div>
          </div>

          <div class="form-group">
            <label>学号:</label>
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text"><i class="far fa-clock"></i></span>
              </div>
              <input type="text" class="form-control float-right" v-model="serial_number" />
            </div>
          </div>

          <div class="form-group">
            <label>姓名:</label>
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text"><i class="far fa-clock"></i></span>
              </div>
              <input type="text" class="form-control float-right" v-model="name" />
            </div>
          </div>

          <div class="form-group">
            <label>邮箱:</label>
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text"><i class="far fa-clock"></i></span>
              </div>
              <input type="text" class="form-control float-right" v-model="email" />
            </div>
          </div>

          <div class="form-group">
            <label>班级:</label>
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text"><i class="fas fa-laptop"></i></span>
              </div>
              <select class="custom-select rounded-0" id="exampleSelectRounded0" v-model="organization_id">
                <option v-for="item in organizations" :key="item.id" class="select-option" :label="item.name" :value="item.id">{{item.name}}</option>
              </select>
            </div>
          </div>

          <div class="form-group">
            <label>邀请码:</label>
            <div class="input-group">
              <div class="input-group-prepend">
                <span class="input-group-text"><i class="far fa-clock"></i></span>
              </div>
              <input type="text" class="form-control float-right" v-model="invite_code" />
            </div>
          </div>

          <button type="button" class="btn btn-block btn-primary" @click="submit()">
            提交
          </button>

          <p class="mb-1 mt-3 float-right">
            <router-link to="login">立即登录</router-link>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/server/api";
import { Notification } from "element-ui";
export default {
  inject: ["reload"],
  data() {
    return {
      id_card_number: "",
      serial_number: "",
      email: "",
      face_img: "",
      name: "",
      invite_code: "",
      organization_id: null,
      organizations: "",
      defaultImg: "",
    };
  },
  methods: {
    uploadFile() {
      this.$refs.uploadFile.click();
    },
    getOrganizations() {
      this.$req.fetch(API.organization.list.path).then((resp) => {
        if (resp.code == 1) {
          this.organizations = resp.data;
        }
      });
    },
    validForm() {
      if (this.face_img == "") {
        return "请上传人脸照片";
      }
      if (this.id_card_number == "") {
        return "请填写身份证号";
      }
      if (this.serial_number == "") {
        return "请填写学号";
      }
      if (this.invite_code == "") {
        return "请填写邀请码";
      }
      if (this.name == "") {
        return "请填写姓名";
      }
      if (this.email != "") {
        var reg = /^\w+\@+[0-9a-zA-Z]+\.(com|com.cn|edu|hk|cn|net)$/;
        if (!reg.test(this.email)) {
          return "邮箱格式错误";
        }
      }
      if (this.organization_id == "") {
        return "请选择预约班级";
      }
      return true;
    },
    submit() {
      var res = this.validForm();
      if (res != true) {
        Notification({
          title: res || "信息填写无效，请仔细检测",
          type: "warning",
        });
        return;
      }
      this.$req
        .post(API.face.create.path, {
          name: this.name,
          id_card_number: this.id_card_number,
          serial_number: this.serial_number,
          email: this.email,
          // face_img: this.face_img.replace(/^data:image\/\w+;base64,/, ""),
          face_img: this.face_img,
          invite_code: this.inviteCode,
          organization_id: this.organization_id,
        })
        .then((resp) => {
          this.reload();
        });
    },
    change(e) {
      var that = this;

      let file = e.target.files[0];
      var reader = new FileReader();

      reader.readAsDataURL(file);
      reader.onload = (e) => {
        that.face_img = e.target.result;
      };
    },
  },
  created() {
    // 获取默认显示的图片
    this.defaultImg = require("assets/img/profile.png");
  },
  mounted() {
    this.getOrganizations();
  },
};
</script>

<style scoped>
.face-circle {
  width: 100px;
  height: 100px;
}
.page-container {
  display: flex;
  /* width: 100vw; */
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.appointment-form {
  width: 560px;
}
.multiple-select {
  width: 475px;
}
</style>
